.loginsignup{
    width:100%;
    height: 110vh;
    background: #fce3fe;
    padding-top: 100px;
}
.loginsignup-container{
    width: 580px;
    height: 620px;
    background:white;
    margin: auto;
    padding: 40px 60px;
}
.loginsignup-container h1{
    margin: 20px 0px;
}
.loginsignup-fields{
    display:flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
}
.loginsignup-fields input{
    height: 72px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9 ;
    outline:none;
    color:#5c5c5c;
    font-size: 18px;
}
.loginsignup-container button{
    width: 470px;
    height: 72px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border:none;
    font-size: 24px;
    font-weight: 500;
    cursor:pointer;
}
.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size:18px;
    font-weight: 500;
}
.loginsignup-login span{
    color: #ff4141;
    font-weight:600;
}
.login:hover{
    cursor:pointer;
    color:#980b0b

}
.continue:hover{
    cursor:pointer;
     background-color: #5f2222;
     transform:scale(1.03);
     transition: all 0.2s;
}
.loginsignup-agree{
    display:flex;
    align-items:center;
    margin-top:25px;
    gap:20px;
    color:#5c5c5c;
    font-size: 18px;
    font-weight:500;
}
