img{
    width:100%;
}
.dropdown{
    width:10px;
}
.shopcategory-banner{
    display:block;
    margin: 30px auto;
    width:82%;
}
.shopcategory-indexSort{
    display:flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}
.shopcategory-sort{
    padding: 10px 20px;
    border-radius: 40px;
    border:1px solid #888;
}
.shopcategory-indexSort p span{
    font-weight: 600;
}
.shopcategory-products{
    margin:20px 100px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap:80px;
    column-gap:24px;
    padding-right: 30px;
    position:relative;
    right:78px;
}

.shopcategory-loadmore{
    display:flex;
    justify-content:center;
    align-items:center;
    margin: 100px auto;
    width: 233px;
    height:59px;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
    cursor:pointer;
}